
.FrontScreen{
    height: 400px;
    width: 600px;
    background-color: rgba(0, 0, 0, 0.596);
    z-index: 4;
  position:absolute;
  top:150px;
  left:350px;
  color:white;
  /* display: flex;
  flex-wrap: wrap; */

}

.zoomedScreen{
    max-width:800px;
    max-height:600px;
    position: absolute;
    top:0px;
    left:0px;
    transform:scale(1.2);
}

.zoomedScreen img{
    max-width:800px;
    max-height:600px;
}



.TransparentBackground{
    background-color: rgba(0, 0, 0, 0.486);
    width:1300px;
    height: 700px;
    z-index: 3;
    backdrop-filter:blur(3px);
}

.image{
    float:left;
    margin:5px;
cursor:zoom-in;
}

.text{
    height:250px;
    font-size:1.1rem;
    text-indent: 2rem;
    float:left;
    overflow-wrap: break-word;
    overflow: auto;
}

.outside{
    float: left;
    shape-outside: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
    width: 200px;
    height: 150px;
}

.bottom {
    border-top:1px solid white;
    border-bottom:1px solid white;
    width:600px;
    height:100px;
    position: absolute;
    bottom:0;
    display: flex;
    justify-content: space-around;
    overflow-y: auto;
}

.close {
    font-size: 3rem;
    width: 4rem;
    height: 4rem;
    text-align: center;
    color:rgb(145, 3, 3);
    position:absolute;
    top:-50px;
    right:-50px;
    background-color: rgba(255, 255, 255, 0.37);
    border-radius: 15px;
    font-weight: bolder;
    cursor: pointer;
}

.close:hover{
    filter:brightness(50%);
}

