.background {
    height:700px;
    width:1300px;
    background-image: url("background.jpg");
    position: relative;
    margin: auto;
}

.text {
    font-size:1.5rem;
    font-weight: bold;
    font-style: italic;
    color:rgb(255, 255, 255);
    position:absolute;
    border-bottom: 2px double rgb(255, 255, 255);
    text-shadow: 2px 2px 4px #000;
    cursor: pointer;
    transition: all 1s ease;
    z-index: 0;
}

.text:hover{
    transform: scale(1.2);
    transition: all 1s ease;
}

